import React, { useState, useEffect, useRef, useCallback } from 'react';
import { Dialog } from '@headlessui/react';
import { EyeIcon, EyeSlashIcon } from '@heroicons/react/24/outline';
import { LZMA } from 'lzma/src/lzma_worker.js';


const defaultUserMapping = {
  "U05LXUSTVU7": "Vinh Quang", "U03P0S6JM7H": "Vũ Tuấn Trường", "U03Q57KQB8Q": "Phạm Băng Đăng", 
  "U04MSK7BV6K": "Hai Pham", "U03P0S6H58X": "Dao Minh Quan", "U01GP6EKHC3": "Phùng Tiến Hào", 
  "U01HCR4QD88": "Đào Tuấn Trung", "U04PVQA4FMM": "Tran Dieu Uy", "U03PFE3Q2SE": "Nguyễn Hoàng Thuận", 
  "U05LXUTRA15": "Trần Quốc Bảo", "U04N478K3S9": "Nguyen Van Viet", "U04N0H9MJ2J": "Nguyễn Đức Anh Phúc", 
  "U06M6HLB9HQ": "Phong Nguyen", "U04N726MYQ3": "Lưu Nguyễn Hoàng Minh", "U05MEU43E5A": "Nguyễn Trọng Tùng", 
  "U05MCF53QUA": "Đức Anh", "U04NWRXL5R6": "Nguyễn Hồ Quang", "U012JQ88ADN": "Lưu Anh Tuấn", 
  "U05M5RP7HFG": "Nguyễn Minh Hùng", "U05MCF4ELLS": "Le Pham Nhat Quynh", "U04QAAKABHQ": "Do Thanh Tung", 
  "U0267DLK160": "Nguyễn Đức Minh Khôi", "U06QTM72XQS": "Nguyen Hoai Ngan", "UM4RDLTGS": "Trần Tuấn Anh"
};


const SlackMessageViewer = () => {
  const [messages, setMessages] = useState([]);
  const [userMapping, setUserMapping] = useState(defaultUserMapping);
  const [isOpen, setIsOpen] = useState(false);
  const [arxivPreviews, setArxivPreviews] = useState({});
  const [showAbstracts, setShowAbstracts] = useState({});
  const [compressedUrl, setCompressedUrl] = useState('');
  const [error, setError] = useState(null);
  const observerRef = useRef();

  useEffect(() => {
    const hash = window.location.hash.substr(1);
    if (hash) {
      console.log("Hash found:", hash);
      decompressAndSetMessages(hash);
    } else {
      console.log("No hash found in URL");
    }
  }, []);

  const decompressAndSetMessages = (base64) => {
    console.log("Starting decompression process");
    fetch(`data:application/octet-stream;base64,${base64}`)
      .then(r => {
        console.log("Fetch response:", r);
        return r.arrayBuffer();
      })
      .then(arrayBuffer => {
        console.log("ArrayBuffer:", arrayBuffer);
        const compressed = new Uint8Array(arrayBuffer);
        console.log("Compressed data (first 10 bytes):", compressed.slice(0, 10));
        LZMA.decompress(compressed, (result, error) => {
          if (error) {
            console.error('LZMA decompression failed:', error);
            setError(`Decompression failed: ${error}`);
          } else {
            console.log("Decompressed result:", result);
            try {
              const decompressedJSON = JSON.parse(result);
              console.log("Parsed JSON:", decompressedJSON);
              setMessages(decompressedJSON);
              setError(null);
            } catch (e) {
              console.error('Failed to parse decompressed JSON:', e);
              setError(`Failed to parse JSON: ${e.message}`);
            }
          }
        });
      })
      .catch(err => {
        console.error("Error in fetch or processing:", err);
        setError(`Error processing data: ${err.message}`);
      });
  };

  const handleFileUpload = (event) => {
    const file = event.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        try {
          const content = JSON.parse(e.target.result);
          setMessages(content);
          compressAndGenerateUrl(content);
          setError(null);
        } catch (err) {
          console.error("Error parsing uploaded file:", err);
          setError(`Error parsing uploaded file: ${err.message}`);
        }
      };
      reader.readAsText(file);
    }
  };

  const compressAndGenerateUrl = (data) => {
    console.log("Starting compression process");
    const jsonString = JSON.stringify(data);
    console.log("JSON string length:", jsonString.length);
    LZMA.compress(jsonString, 1, (compressed, error) => {
      if (error) {
        console.error('LZMA compression failed:', error);
        setError(`Compression failed: ${error}`);
      } else {
        console.log("Compressed data length:", compressed.length);
        const reader = new FileReader();
        reader.onload = () => {
          const base64 = reader.result.split(',')[1];
          console.log("Base64 length:", base64.length);
          const url = `${window.location.origin}${window.location.pathname}#${base64}`;
          setCompressedUrl(url);
          setError(null);
        };
        reader.readAsDataURL(new Blob([new Uint8Array(compressed)]));
      }
    });
  };

  const handleUserMappingChange = (event) => {
    try {
      const mapping = JSON.parse(event.target.value);
      setUserMapping({ ...defaultUserMapping, ...mapping });
    } catch (error) {
      console.error('Invalid JSON for user mapping');
    }
  };

  const extractArxivLinks = (text) => {
    const arxivRegex = /arxiv\.org\/abs\/(\d+\.\d+)/g;
    return [...text.matchAll(arxivRegex)].map(match => match[1]);
  };

  const fetchArxivPreview = useCallback(async (arxivId) => {
    if (arxivPreviews[arxivId]) return;

    try {
      // Using a CORS proxy
      const proxyUrl = 'https://cors-anywhere.herokuapp.com/';
      const targetUrl = `http://export.arxiv.org/api/query?id_list=${arxivId}`;
      const response = await fetch(proxyUrl + targetUrl);
      const data = await response.text();
      const parser = new DOMParser();
      const xmlDoc = parser.parseFromString(data, "text/xml");
      
      const title = xmlDoc.querySelector("entry > title").textContent;
      const summary = xmlDoc.querySelector("entry > summary").textContent;
      
      setArxivPreviews(prev => ({
        ...prev,
        [arxivId]: { title, summary }
      }));
    } catch (error) {
      console.error('Error fetching arXiv preview:', error);
    }
  }, [arxivPreviews]);

  const toggleAbstract = (arxivId) => {
    setShowAbstracts(prev => ({
      ...prev,
      [arxivId]: !prev[arxivId]
    }));
  };

  const renderText = (text) => {
    const mentionRegex = /<@(U[A-Z0-9]+)>/g;
    const linkRegex = /<(https?:\/\/[^>]+)>/g;
    
    let renderedText = text.replace(mentionRegex, (match, userId) => {
      const userName = userMapping[userId] || userId;
      return `@${userName}`;
    });

    renderedText = renderedText.replace(linkRegex, (match, url) => url);

    const parts = renderedText.split(/(https?:\/\/\S+)/);

    return (
      <>
        {parts.map((part, index) => {
          if (part.match(/^https?:\/\//)) {
            if (part.includes('arxiv.org/abs/')) {
              const arxivId = part.split('/').pop();
              const preview = arxivPreviews[arxivId];
              return (
                <div key={index} className="my-2 p-2 bg-gray-100 rounded" ref={el => {
                  if (el && !preview) {
                    observerRef.current.observe(el);
                  }
                }}>
                  <a href={part} target="_blank" rel="noopener noreferrer" className="text-blue-500 hover:underline">{part}</a>
                  {preview && (
                    <div className="mt-1">
                      <h4 className="font-bold">{preview.title}</h4>
                      <button 
                        onClick={() => toggleAbstract(arxivId)}
                        className="text-sm text-blue-500 hover:underline flex items-center"
                      >
                        {showAbstracts[arxivId] ? (
                          <>
                            <EyeSlashIcon className="h-4 w-4 mr-1" />
                            Hide Abstract
                          </>
                        ) : (
                          <>
                            <EyeIcon className="h-4 w-4 mr-1" />
                            Show Abstract
                          </>
                        )}
                      </button>
                      {showAbstracts[arxivId] && <p className="text-sm mt-1">{preview.summary}</p>}
                    </div>
                  )}
                </div>
              );
            } else {
              return <a key={index} href={part} target="_blank" rel="noopener noreferrer" className="text-blue-500 hover:underline">{part}</a>;
            }
          } else {
            return <span key={index}>{part}</span>;
          }
        })}
      </>
    );
  };

  const renderMessage = (message) => {
    const userName = userMapping[message.user] || message.user;
    return (
      <div key={message.ts} className="mb-4 p-4 bg-white rounded-lg shadow">
        <div className="flex items-center mb-2">
          <span className="font-bold mr-2">{userName}</span>
          <span className="text-gray-500 text-sm">{new Date(parseFloat(message.ts) * 1000).toLocaleString()}</span>
          {message.reply_count > 0 && (
            <span className="ml-2 bg-blue-100 text-blue-800 text-xs font-medium mr-2 px-2.5 py-0.5 rounded">
              {message.reply_count} {message.reply_count === 1 ? 'reply' : 'replies'}
            </span>
          )}
        </div>
        <div>{renderText(message.text)}</div>
        {message.reactions && (
          <div className="mt-2 flex flex-wrap">
            {message.reactions.map((reaction, index) => (
              <span key={index} className="bg-gray-100 rounded-full px-2 py-1 text-sm mr-2 mb-2">
                :{reaction.name}: {reaction.count}
              </span>
            ))}
          </div>
        )}
      </div>
    );
  };

  useEffect(() => {
    observerRef.current = new IntersectionObserver(
      (entries) => {
        entries.forEach(entry => {
          if (entry.isIntersecting) {
            const arxivId = entry.target.querySelector('a').href.split('/').pop();
            fetchArxivPreview(arxivId);
            observerRef.current.unobserve(entry.target);
          }
        });
      },
      { rootMargin: '100px' }
    );

    return () => {
      if (observerRef.current) {
        observerRef.current.disconnect();
      }
    };
  }, [fetchArxivPreview]);

  return (
    <div className="container mx-auto p-4">
      <h1 className="text-2xl font-bold mb-4">Slack Message Viewer</h1>
  
      <div className="bg-yellow-100 border-l-4 border-yellow-500 text-yellow-700 p-4 mb-4" role="alert">
        <p className="font-bold">Note:</p>
        <p>If you encounter an error, please first visit <a href="https://cors-anywhere.herokuapp.com/" target="_blank" rel="noopener noreferrer" className="underline">https://cors-anywhere.herokuapp.com/</a> to enable the API.</p>
      </div>
      
      <div className="mb-4">
        <label className="block text-sm font-medium text-gray-700">Upload JSON File</label>
        <input type="file" onChange={handleFileUpload} className="mt-1 block w-full" />
      </div>
      
      {compressedUrl && (
        <div className="mb-4 p-4 bg-green-100 border-l-4 border-green-500 text-green-700">
          <p className="font-bold">Shareable URL:</p>
          <a href={compressedUrl} target="_blank" rel="noopener noreferrer" className="break-all">
            {compressedUrl}
          </a>
        </div>
      )}
      
      <div className="mb-4">
        <label className="block text-sm font-medium text-gray-700">Additional User Mapping (JSON)</label>
        <textarea 
          onChange={handleUserMappingChange}
          className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-300 focus:ring focus:ring-indigo-200 focus:ring-opacity-50"
          rows="4"
          placeholder="Enter additional user mappings here..."
        ></textarea>
      </div>
      
      <button 
        onClick={() => setIsOpen(true)}
        className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded"
      >
        Rerender
      </button>
      
      <Dialog open={isOpen} onClose={() => setIsOpen(false)} className="relative z-50">
        <div className="fixed inset-0 bg-black/30" aria-hidden="true" />
        <div className="fixed inset-0 flex items-center justify-center p-4">
          <Dialog.Panel className="w-full max-w-sm rounded bg-white p-4">
            <Dialog.Title>Messages Rerendered</Dialog.Title>
            <p>The messages have been updated with the new user mapping.</p>
            <button 
              onClick={() => setIsOpen(false)}
              className="mt-4 bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded"
            >
              Close
            </button>
          </Dialog.Panel>
        </div>
      </Dialog>
      
      <div className="mt-8">
        {messages.map(renderMessage)}
      </div>
    </div>
  );
};

export default SlackMessageViewer;
